



























import { Component, Vue } from 'vue-property-decorator'

@Component({})
export default class PhotoWinow extends Vue {
  public a: any = 1
  private isShow: boolean = false
  private isBig: boolean = false
  private isGo: boolean = false

  public mounted() {
    setTimeout(() => {
      this.isShow = !this.isShow
      setTimeout(() => {
        this.isBig = !this.isBig
        setTimeout(() => {
          this.isGo = !this.isGo
        }, 3000)
      }, 2000)
    }, 1000)
  }
}
